/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>I am currently researching a field of design known as \"Participatory Design\" that has a fascinating history (dealing with Scandinavian labor unions) and a very promising future. In short,  PD is about incorporating the user in the design process from day one. The resulting ideas and workflows are, in my mind, incredibly powerful tools for working on any project. My focus is web development. </p>\n<p>Most of my interest in this concept comes from my day job at a nonprofit evaluation firm — we do \"collaborative evaluation\" to help programs develop. The concepts are largely the same, and I'm just trying to apply them to web design and the development of usable, useful online tools.</p>\n<p>This process is intimately involved with many of the same user-centered concerns that preoccupy other ICT-development folks, such as the advocates of free and open-source software. </p>\n<p>The center of the world for PD is the organization <a href=\"\nhttp://www.cpsr.org/ \">Computer Professionals for Social Responsibility</a>. They have <a href=\"\nhttp://www.cpsr.org/prevsite/program/workplace/PD-resources.html   \">a site dedicated to PD resources</a>.</p>","frontmatter":{"created_at":"October 03, 2005","path":"/2005/10/03/participatory-design","title":"Participatory Design","tags":["design","participatory"]}}},"pathContext":{}}

/***/ })

